.batchForm {
  margin-top: 16px;
  border: 2px solid royalblue;
  border-radius: 16px;
  padding: 16px;

  .title {
    background: royalblue;
    color: #FFF;
    border-radius: 8px;
  }

  .form {
    margin-top: 16px;

    .row {
      display: flex;
      align-items: center;

      &:not(:first-child) {
        margin-top: 8px;
      }

      .field {
        margin-left: 4px;

        input {
          padding: 2px 14px;
        }
      }

      .icon {
        margin-left: 8px;
        display: block;
        width: 44px;
        height: 44px;
        border: 2px solid #223472;
        border-radius: 4px;
        box-sizing: border-box;
      }

    }

    .btn {
      margin-top: 16px;

      .preloaderWrapper {
        position: relative;

        .preloader {
          width: 25px;
          height: 25px;
          position: absolute;
          transform: translate(100%, -50%);
          top: 50%;
          right: -8px;

          svg {
            color: #FFF;
          }
        }
      }
    }

  }
}
