.priceForm {
  margin-top: 12px;
  display: flex;
  align-items: center;

  .icon {
    display: block;
    width: 44px;
    height: 44px;
    border: 2px solid #223472;
    border-radius: 4px;
    box-sizing: border-box;
  }

  .field {
    margin-left: 8px;

    input {
      padding: 2px 14px;
    }
  }

  .btn {
    margin-left: 8px;
    width: 44px;
    height: 44px;
    padding: 0;
  }

}
