@import "../../assets/style/app";

.shop {
  position: relative;
  padding: 16px;

  .progress {
    @include progress;
  }

  .borderBlock {
    margin-top: 16px;
    background: #EEE;
    border: 2px solid royalblue;
    border-radius: 16px;
    padding: 16px;

    .blockTitle {
      background: #2d4f96; //#121e3e;
      color: #FFF;
      border-radius: 8px;
    }
  }

  .infoBlockColumn {
    @include infoBlockColumn
  }
}
