.balances {
  margin-top: 16px;
  //border: 2px solid royalblue;
  //border-radius: 16px;
  //padding: 8px;

  .items {
    display: grid;
    grid-template-columns: repeat(5, 1fr);

    .item {
      display: flex;
      flex-direction: column;
      align-items: center;

      .imgWrapper {
        border: 4px solid #121e3e;
        border-radius: 16px;
        padding: 2px;

        img {
          display: block;
          width: 75px;
          height: 75oopx;
          border-radius: 12px;
        }
      }

      .label {
        margin-top: 8px;
        font-weight: bold;
        font-size: 24px;
        color: #ac00ba;
      }

      .price {
        margin-top: 4px;

        span {
          color: #ac00ba;
          font-weight: bold;
        }

      }
    }
  }


}
